import './Table.css';
import { CardContent,Card,Typography } from '@material-ui/core';
import React,{useState,useEffect} from 'react'
import numeral from "numeral";

function Table({continent = 'Asia',Allcountries}) {
    const [raised,setraised]=useState(false);
    const [country,setCountry] = useState('India');
    const [countryDetails,setContryDetails]=useState();
  
   useEffect(()=>{
              const fetchCountrydetails =async()=>{
               await fetch("https://disease.sh/v3/covid-19/countries")
                  .then((response) => response.json())
                  .then((data) => {
                       data.map((Mycountry)=>{
                        if(Mycountry.continent === continent && Mycountry.country === country){
                         setContryDetails(Mycountry);
                         console.log("country details",Mycountry);
                        }
                       })
                  });
               }
               fetchCountrydetails();
    },[country]);
    
    function toggleRaised(e){
        e.preventDefault();
        setraised(!raised);
    }
    return (
        <div className="table">
          <Card className="top_right_card" style={{backgroundColor: "goldenrod"}} 
              onMouseOver={toggleRaised} 
              onMouseOut={toggleRaised} raised={raised}>
                         <CardContent>
                                <Typography color="textPrimary" gutterBottom style={{fontSize:'20px'}}>Continent : {continent}</Typography>
                                <hr/>
                                <div className="table01">
                                    <tr>
                                      <th><Typography className="thead" color="textPrimary">Countries</Typography></th>
                                      <th><Typography className="thead" color="textPrimary">Active Cases</Typography></th>
                                    </tr>
                                { Allcountries && Allcountries.map((country,index) => (
                                    country.continent === continent && (
                                    <tr>
                                    <td><Typography color="textPrimary" key={index} onClick={()=>{setCountry(country.country)}}>{country.country}</Typography>
                                    </td>
                                    <td>
                                        <Typography color="textPrimary" key={index}>{country.cases}</Typography>
                                    </td>
                                    </tr>)
                                ))}
                                </div>
                         </CardContent>
          </Card>  
          <Card style={{height:'240px'}} onMouseOver={toggleRaised} 
              onMouseOut={toggleRaised} raised={raised}>
                         <CardContent>
                          <Typography color="textPrimary" gutterBottom className="info-confirmed"><strong>Country Information:</strong></Typography>
                         
                         {countryDetails &&(<div className="info-container">
                             <div
                                className="info-flag"
                                style={{ backgroundImage: `url(${countryDetails.countryInfo.flag})` }}
                            ></div>
                            <div className="info-name">{countryDetails.country}({countryDetails.countryInfo.iso3})</div>
                            <div className="info-confirmed">
                                continent: {countryDetails.continent}
                            </div>
                            <div className="info-confirmed">
                                Population: {numeral(countryDetails.population).format("0.0a")}
                            </div>
                            <div className="info-confirmed">
                                Active Cases: {numeral(countryDetails.active).format("0.0a")}
                            </div>
                            </div>)}
                          </CardContent>
            </Card>
        </div >
    )
}

export default Table
