import './PieChart.css';
import React,{useState} from 'react'
import { CardContent,Card,Typography } from '@material-ui/core';
import { Doughnut } from 'react-chartjs-2';
import numeral from 'numeral'
const chartColors = [
  "green",
  "cornflowerblue",
  "crimson"
];

const options = {
  legend: {
    display: true,
    position: "left"
  },
  elements: {
    arc: {
      borderWidth: 1
    }
  }
};
const options1=options;

function PieChart({data}) {
const [raised1,setraised1]=useState(false);
const [raised2,setraised2]=useState(false);
const convertstat = (stat) =>
  stat ? `+${numeral(stat).format("0.0a")}` : "+0";

const mydata = {
  maintainAspectRatio: false,
  responsive: false,
  labels: ["Total Cases: "+convertstat(data.cases), "rotal Recovered: "+convertstat(data.recovered), "Total Deaths: "+convertstat(data.deaths)],
  datasets: [
    {
      data: [data.cases,data.recovered,data.deaths],
      backgroundColor: chartColors,
      hoverBackgroundColor: chartColors
    }
  ]
};
const mydata1 = {
  maintainAspectRatio: false,
  responsive: false,
  labels: ["Today's Cases: "+data.todayCases, "Today's Recovered: "+data.todayRecovered, "Today's Deaths: "+data.todayDeaths],
  datasets: [
    {
      data: [data.todayCases+1,data.todayRecovered+1,data.todayDeaths+1],
      backgroundColor: chartColors,
      hoverBackgroundColor: chartColors
    }
  ]
};
function toggleRaised1(e){
        e.preventDefault();
        setraised1(!raised1);
    }
    function toggleRaised2(e){
        e.preventDefault();
        setraised2(!raised2);
    }
    return (
        <div className="bottom_card" >
            <Card style={{height:'190px'}} onMouseOver={toggleRaised1} 
              onMouseOut={toggleRaised1} raised={raised1}>
                         <CardContent>
                          <Typography color="textPrimary" gutterBottom>DoughNut chart:Total
                          </Typography>
                          <Typography color="textPrimary" gutterBottom>
                          <Doughnut data={mydata} options={options} />
                           </Typography>
                         </CardContent>
            </Card>
            <Card style={{height:'190px'}} onMouseOver={toggleRaised2} 
              onMouseOut={toggleRaised2} raised={raised2}>
                         <CardContent>
                          <Typography color="textPrimary" gutterBottom>DoughNut chart:Today
                          </Typography>
                          <Typography color="textPrimary" gutterBottom>
                          <Doughnut data={mydata1} options={options1} />
                          </Typography>
                         </CardContent>
            </Card>
        </div>
    )
}

export default PieChart;

