import './WorldMap.css';
import React,{useState} from 'react';
import { MapContainer  as LeafletMap , TileLayer,useMap,Circle, Popup } from "react-leaflet";
import "leaflet/dist/leaflet.css";
import numeral from "numeral";

const casesTypeColors = {
  cases: {
    hex: "#CC1034",
    multiplier: 300,
  },
  recovered: {
    hex: "#7dd71d",
    multiplier: 200,
  },
  deaths: {
    hex: "#fb4443",
    multiplier: 500,
  },
};

function WorldMap({countries,casesType, center, zoom}) {
  
    function ChangeView({ center, zoom }) {
        const map = useMap();
        map.setView(center, zoom);
        return null;
     }
     
  console.log("case types",casesTypeColors[casesType].hex);
    return (
    <div className="map">
      <LeafletMap center={center} zoom={zoom} scrollWheelZoom={false}>
      <ChangeView center={center} zoom={zoom} /> 
        <TileLayer
          url="https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png"
          attribution='&copy; <a href="http://osm.org/copyright">OpenStreetMap</a> contributors'
        />
        {countries && countries.map((country,index) => (
            <Circle
            center={[country.countryInfo.lat, country.countryInfo.long]}
            pathOptions={{color: casesTypeColors[casesType].hex,
                     fillColor: casesTypeColors[casesType].hex }}
            fillOpacity={0.5}
            radius={
                Math.sqrt(country[casesType]) * casesTypeColors[casesType].multiplier
            }
            key={index}
            >
            <Popup style={{height:'100px'}}>
                    <div className="info-container">
                    <div
                        className="info-flag"
                        style={{ backgroundImage: `url(${country.countryInfo.flag})` }}
                    ></div>
                    <div className="info-name">{country.country}({country.countryInfo.iso2})</div>
                    <div className="info-confirmed">
                        continent: {country.continent}
                    </div>
                    <div className="info-confirmed">
                        Population: {numeral(country.population).format("0.0a")}
                    </div>
                    <div className="info-confirmed">
                        Cases: {numeral(country.cases).format("0.0a")}
                    </div>
                    <div className="info-recovered">
                        Recovered: {numeral(country.recovered).format("0.0a")}
                    </div>
                    <div className="info-deaths">
                        Deaths: {numeral(country.deaths).format("0.0a")}
                    </div>
                    </div>
                </Popup>
            </Circle>
        ))};
      </LeafletMap>
    </div>
    )
}

export default WorldMap


 