import React,{useState,useEffect} from 'react';
import './App.css';
import { Select,MenuItem,FormControl,CardContent,Card,Typography,InputLabel } from '@material-ui/core';
import Table from './components/Table';
import PieChart from './components/PieChart';
import Bar from './components/Bar';
import LineChart from './components/Line';
import CasesBox from './components/CasesBox';
import WorldMap from './components/WorldMap';
import numeral from "numeral";

function App() {
   const [continents,setContinents] = useState([]);
   const [continent,setContinent] = useState();
   const [countries,setcountries] = useState([]);
   const [country,setcountry] = useState('India');
   const [countryInfo,setcountryInfo] = useState({});
   const [all_countries_data,setData] =useState([]);
   const [Center, setCenter] = useState({ lat:20, lng: 70 });
   const [Zoom, setZoom] = useState(4);
   const [casesType, setCasesType] = useState("cases");

 const prettyPrintStat = (stat) =>
  stat ? `+${numeral(stat).format("0.0a")}` : "+0";

   useEffect(()=>{
    const fetchContinents =async()=>{
      await fetch("https://disease.sh/v3/covid-19/continents")
      .then(response=>response.json())
      .then(data=>{
        console.log("data>>",data);
        setContinents(data);
      })
    }

    fetchContinents();

      const fetchCountrydetails =async()=>{
      await fetch("https://disease.sh/v3/covid-19/countries")
                  .then((response) => response.json())
                  .then((data) => {
                        setData(data);
                  });
      }

    fetchCountrydetails();

   const fetchDefaultCountry =async()=>{
    await fetch(`https://disease.sh/v3/covid-19/countries/${country}`)
            .then((response) => response.json())
            .then((data) => {
                  setcountryInfo(data);
            });
   }
   fetchDefaultCountry();
   },[]);

   useEffect(()=>{
      if(continent){
       continents.map(mycontinent=>{
             if(mycontinent.continent===continent){
              setcountries(mycontinent.countries);
              console.log("countries>>",mycontinent.countries);
             }
       });
      }
   },[continent]);

   function selectContinent(e){
         setContinent(e.target.value);
   }

   async function selectCountry(e){
      console.log("value>>>",e.target.value)
      setcountry(e.target.value);
      await fetch(`https://disease.sh/v3/covid-19/countries/${e.target.value}`)
            .then((response) => response.json())
            .then((data) => {
                  setcountryInfo(data);
                  setCenter({...Center,['lat']:data.countryInfo.lat,['lng']:data.countryInfo.long});
                  setZoom(4);
            });
     }
     
  return (
    <div className="App">
              <div className="App_top">
                    <div className="App_left">
                       <div className="App_left_header">
                          <h2 className="App_header_text">React COVID-19 Tracker</h2>
                          {continents && 
                          <FormControl className="App_dropdown" variant="filled">
                          <InputLabel id="continent-label">Continents</InputLabel>
                          <Select labelId="continent-label" id="continent-label" value={continent} onChange={selectContinent}>
                                {continents.map((continent,index) => (
                                      <MenuItem key={index} value={continent.continent}>{continent.continent}</MenuItem>
                                ))}
                          </Select>
                          </FormControl>}
                          <FormControl className="App_dropdown" variant="filled">
                          <InputLabel id="countries-label">Countries</InputLabel>
                          <Select labelId="countries-label" id="countries-label" value="worldWide" disabled={!continent} value={country} onChange={selectCountry}>
                                {!continent && <MenuItem value={country}>{country}</MenuItem>}
                                {countries.map((country,index) => (
                                      <MenuItem key={index} value={country}>{country}</MenuItem>
                                ))}
                          </Select>
                          </FormControl>
                       </div>
                       <div className="App_left_boxes">
                         <CasesBox  Title="CoronaVirus Cases" cases={prettyPrintStat(countryInfo.todayCases)} 
                         Total={prettyPrintStat(countryInfo.cases)} backgroundcolor="green"  onClick={(e) => setCasesType("cases")} />
                         <CasesBox Title="Recoveries" cases={prettyPrintStat(countryInfo.todayRecovered)} 
                         Total={prettyPrintStat(countryInfo.recovered)} backgroundcolor="whitesmoke" onClick={(e) => setCasesType("recovered")} popup/>
                         <CasesBox Title="Deaths" cases={prettyPrintStat(countryInfo.todayDeaths)} 
                         Total={prettyPrintStat(countryInfo.deaths)} backgroundcolor="crimson" onClick={(e) => setCasesType("deaths")}/>
                       </div> 
                       <WorldMap
                        countries={all_countries_data}
                        casesType={casesType}
                        center={Center}
                        zoom={Zoom}
                        />
                    </div>
                    <div className="App_right">
                       <Table continent={continent} Allcountries={all_countries_data}/>
                       <div className="App_right_chart">
                         <LineChart casesType={casesType} country={country}/> 
                         <br/>
                         <PieChart data={countryInfo}/>
                       </div>
                    </div>
              </div>
    </div>
  );
}

export default App;
