import './CasesBox.css';
import React,{useState} from 'react'
import { CardContent,Card,Typography } from '@material-ui/core';

function CasesBox({Title,cases=0,Total=0,backgroundcolor,onClick,popup}) {
    const [raised,setraised]=useState(false);
    function toggleRaised(e){
        e.preventDefault();
        setraised(!raised);
    }

    return (
         <Card className="covid_cases" style={{backgroundColor: backgroundcolor,height:'160px'}} onClick={onClick}
              onMouseOver={toggleRaised} 
              onMouseOut={toggleRaised} raised={raised}>
                <CardContent>
                <Typography color="textPrimary" gutterBottom style={{fontSize:'20px'}}>{Title}</Typography>
                <h3 className="covid_case">{cases}</h3>
                <Typography color="textSecondary" gutterBottom>Total: {Total}</Typography>
                </CardContent>

        </Card> 
    )
}

export default CasesBox
