import './Line.css';
import React ,{ useState, useEffect } from 'react'
import { CardContent,Card,Typography } from '@material-ui/core';
import { Line } from "react-chartjs-2";
import numeral from "numeral";

const options = {
  legend: {
    display: false,
  },
  elements: {
    point: {
      radius: 0,
    },
  },
  maintainAspectRatio: true,
  tooltips: {
    mode: "index",
    intersect: false,
    callbacks: {
      label: function (tooltipItem, data) {
        return numeral(tooltipItem.value).format("+0,0");
      },
    },
  },
  scales: {
    xAxes: [
      {
        type: "time",
        time: {
          format: "MM/DD/YY",
          tooltipFormat: "ll",
        },
      },
    ],
    yAxes: [
      {
        gridLines: {
          display: false,
        },
        ticks: {
          // Include a dollar sign in the ticks
          callback: function (value, index, values) {
            return numeral(value).format("0a");
          },
        },
      },
    ],
  },
};

const buildChartData = (data, casesType) => {
  let chartData = [];
  let lastDataPoint;
  for (let date in data.cases) {
    if (lastDataPoint) {
      let newDataPoint = {
        x: date,
        y: data[casesType][date] - lastDataPoint,
      };
      chartData.push(newDataPoint);
    }
    lastDataPoint = data[casesType][date];
  }
  return chartData;
};

const buildCountryChartData = (data, casesType) => {
  let chartData = [];
  let lastDataPoint;
  for (let date in data.timeline.cases) {
    if (lastDataPoint) {
      let newDataPoint = {
        x: date,
        y: data.timeline[casesType][date] - lastDataPoint,
      };
      chartData.push(newDataPoint);
    }
    lastDataPoint = data.timeline[casesType][date];
  }
  return chartData;
};

function LineChart({casesType="cases",country}) {
     const [data, setData] = useState({});
     const [countrydata, setCountryData] = useState({});
     const [raised1,setraised1]=useState(false);
     const [raised2,setraised2]=useState(false);

    useEffect(() => {
    const fetchData = async () => {
      await fetch("https://disease.sh/v3/covid-19/historical/all?lastdays=120")
        .then((response) => {
          return response.json();
        })
        .then((data) => {
          let chartData = buildChartData(data, casesType);
          setData(chartData);
          console.log(chartData);
          // buildChart(chartData);
        });
    };
   
    fetchData();

    const fetchDataForCountry = async () => {
      await fetch(`https://disease.sh/v3/covid-19/historical/${country}?lastdays=120`)
        .then((response) => {
          return response.json();
        })
        .then((data) => {
          let chartData = buildCountryChartData(data, casesType);
          setCountryData(chartData);
          console.log(chartData);
        });
    };
    fetchDataForCountry();
  }, [casesType]);

  function toggleRaised1(e){
        e.preventDefault();
        setraised1(!raised1);
    }

  function toggleRaised2(e){
        e.preventDefault();
        setraised2(!raised2);
    }
    return (
        <div  className="bottom_card">
         
        <Card style={{height:'220px'}} onMouseOver={toggleRaised1} 
              onMouseOut={toggleRaised1} raised={raised1}>
                <CardContent>
                <Typography color="textPrimary" gutterBottom>Line Chart:Last 120 days stats <strong>worldwide</strong>
                </Typography>
                
                {data?.length > 0 && (
                        <Line
                        data={{
                            datasets: [
                            {
                                backgroundColor: "rgba(204, 16, 52, 0.5)",
                                borderColor: "#CC1034",
                                data: data,
                            },
                            ],
                        }}
                        options={options}
                        />
                    )}
                    
                </CardContent>
        </Card> 
        
        <Card style={{height:'200px'}} onMouseOver={toggleRaised2} 
              onMouseOut={toggleRaised2} raised={raised2}>
                <CardContent>
                <Typography color="textPrimary" gutterBottom>Line Chart:Last 120 days stats <strong>{country}</strong>
                </Typography>
                {countrydata?.length > 0 && (
                    <Line
                    data={{
                        datasets: [
                        {
                            backgroundColor: "rgba(204, 16, 52, 0.5)",
                            borderColor: "#CC1034",
                            data: countrydata,
                        },
                        ],
                    }}
                    options={options}
                    />
                )}
                </CardContent>
        </Card> 
        
        </div>
    )
}

export default LineChart
